const refs = {
    days: document.querySelector('[data-value = days]'),
    hours: document.querySelector('[data-value=hours]'),
    minutes: document.querySelector('[data-value=mins]'),
    seconds: document.querySelector('[data-value=secs]')
};

function myTimer () {
    const newYear = new Date ('jan 1 2021');
    const finishDate = newYear.getTime()
    let timeBetween = finishDate - Date.now();
    
    refs.days.textContent = Math.floor(timeBetween / (1000 * 60 * 60 * 24));
    refs.hours.textContent = Math.floor((timeBetween % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
    refs.minutes.textContent = Math.floor((timeBetween % (1000 * 60 * 60)) / (1000 * 60));
    refs.seconds.textContent = Math.floor((timeBetween % (1000 * 60)) / 1000);  
};

let interval = setInterval(() => {
myTimer()
},1000);



